@import '../../../index.scss';

$headerHeight: 22px;

.addtask-conatainer {
  position: relative;
  .addtask-create-icon {
    position: absolute;
    width: 11px;
    height: 22px;
    margin-left: 3px;
    /* line-height: 22px; */
    font-weight: bold;
    color: #fff;
    left: 20px;
    font-size: 12px;
  }
}

.addtask-content {
  width: 110px;
  height: 22px;
  border-radius: 20px;
  margin-left: 15px;
  color: #ffffff;
  background: $PrimaryBgColor;
  white-space: nowrap;
  height: $headerHeight;
  font-size: 12px;
  font-family: 'Roboto';
  font-stretch: normal;
  font-style: normal;
  line-height: #{$headerHeight}-3;
  letter-spacing: normal;
  text-align: left;
  padding-left: 25px;
  border: transparent;
}
