.Story_Slide_Header {
  padding: 1% 1.5%;
  text-align: left;
  margin-right: -90px;
  // border-bottom: solid 0.5px #ededed;
  white-space: nowrap;
  overflow: hidden;
  padding: 14px;
  .Story1 {
    width: 57%;
    padding-left: 24px;



    
    .Story_Slide_Header_Title {
      font-weight: 500;
      font-size: 14px;
      padding-bottom: 1%;
      text-transform: uppercase;
      font-size: 13px;
      color: #000;
    }
    .Story_Slide_Header_Content {
      font-size: 12px;
      overflow-wrap: break-word;
      width: 100%;
      color: #666666;
    }
   
  }
  .enterprie-button{
    width: 13%;
    font-size: 11px;
    text-transform: uppercase;
    padding: 2px 10px 0px 10px;
    /* padding-left: 10px; */
    /* padding-right: 10px; */
    margin-top: 11px;
    margin-left: 30px;
    height: 17px;
    /* color: #000; */
    border-radius: 10px;
    border: solid 0.5px #e2e2e2;
    background-color: #f8f8f8;
  }
  .iconUpload {
    width: 4%;
    height: 41%;
    border-radius: 50px;
    background-color: blue;
    position: relative;
    /* z-index: 1; */
    right: 0px;
    float: right;
    bottom: 16px;
  }

  .Story2 {
    // position: absolute;
    // right: 5%;
    // top: 30%;
    display: none;

    .pad40 {
      margin-right: 10px;
    }
  }

  &:hover .Story2 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.togglerIcon_story {
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}
