.Enterprice_Main {
  padding: 1px 1px 1px 1px;
  .enterprise-slide-content {
    background-color: white;
  }
  .noenterpriseevent {
    display: flex;
    height: 88vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    color: #c3c3c3;
  }

  .Enterprice_Story_Filter {
    width: 470px;
    height: 24px;
    border-radius: 30px;
    border: solid 0.5px #f0f0f0;
    background-color: #ffffff;
    font-size: 12px;
    color: #999999;
    padding-left: 37px;
    padding-top: 6px;
    padding-bottom: 6px;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    box-sizing: border-box;
  }

  .EventConfigSignalName {
    // width: 100%;
    width: 90%;
  }
  .EventConfigSubscriberName {
    width: 100%;
    padding-left: 17px;
  }
}
.Enterprice_Header {
  height: 60px;
  font-size: 16px;
  color: #666666;
  font-weight: 600;
  padding: 14.5px 14.5px;
}
.Enterprice_Body {
  border-radius: 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  height: calc(100% - 60px);
  // height: 583px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: center;
  /* color: #c3c3c3; */
  -webkit-backdrop-filter: blur(30px);
  backdrop-filter: blur(30px);
  background-color: #f8f8f8;
}
.Enterprice_Body_left {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 33%;
  height: 100%;
  /* height: 100%; */
  /* height: fit-content; */
  box-shadow: 5px 10px 15px -8px grey;
  position: relative;
  z-index: 1;
  background-color: #ffff;
  cursor: pointer;
  .slider-header {
    background-color: #f8f8f8;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
    padding: 9px;
    border-bottom: solid 0.5px #ededed;
  }
}
.Enterprice_Body_right {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  width: 70%;
  border-bottom: solid 2px #ededed;
}
.Enterprise_Add_Button {
  padding: 4% 0% 3.5% 4%;
  color: #4951c3;
  background-color: white;
  border-bottom: 1px solid #d8c9c9;
}

.Enterprice_Body_Filter {
  background-color: #f8f8f8;
  border: solid 0.5px #f0f0f0;
  /* border-radius: 10px; */
  border-top-left-radius: 10px;
  /* border-top-right-radius: 10px; */
  display: flex;
  height: 52px;
  /* margin-bottom: 5px; */
  /* margin-top: -1px; */
  padding: 12px;
  padding-top: 15px;
  padding-right: 12px;
}
.Enterprice_Body_Filter input[type='text'] {
  height: 22px;
  border-radius: 30px;
  border: solid 0.5px #f0f0f0;
