  font-size: 12px;
  color: #999999;
  padding-left: 37px;
  padding-top: 6px;
  padding-bottom: 6px;
  line-height: 1.14;
  letter-spacing: normal;
  text-align: left;
  box-sizing: border-box;
  width: 100%;
}

.AddEvent_Plus_icon {
  float: right;
  padding-right: 4%;
  padding-top: 0.25%;
}

.Enter_Body_left_text {
  width: 60%;
