}
.Enterprice_content {
  padding: 5px 10px;
  border-bottom: solid 1px #ededed;
  background-color: white;
  border-right: 2px solid transparent;
  height: auto;
  padding-bottom: 10px;
}
.Enterprice_content:hover {
  background-color: #ebedfd;
}
.overlay {
  position: absolute;
  bottom: 0;
  left: 1;
  right: 0;
  background-color: #008cba;
  overflow: hidden;
  width: 2%;
  height: 0;
  transition: 0.5s ease;
}
.borderBlue {
  border-right: 2px solid #4a51c3;
  background-color: #ebedfd;
}
.story_img {
  width: 15px;
