.SubscriberDetails {
  height: calc(95vh - 88px);
  overflow: scroll;
  border-bottom: 1px solid #ededed;
}

.Subscriber_Story {
  display: flex;
  line-height: 16px;

  background-color: white;
  margin-top: 7px;
  padding: 1.8%;
  border-bottom: 1px solid #ededed;
}
.Subscriber_view_list {
  // height: calc(56vh - 88px);
  border: solid 1px #f3eaea;
  width: 100%;
  // text-align: left;
  // overflow: scroll;
}
.Subscriber_MainContent {
  padding: 0.5% 2%;
  background-color: white;
  border-bottom: 1px solid #ededed;
  text-align: left;
}
.Subscriber_title {
  padding: 1% 0%;
  font-weight: 600;
  font-family: Roboto;
  font-size: 13px;
  /* font-weight: normal; */
  font-stretch: normal;
  font-style: normal;
  line-height: 2.5;
  letter-spacing: normal;
  text-align: left;
  color: var(--black);
}
.Subscriber_Content {
  color: #666666;
  font-size: 12px;
}
