.main-tab {
  padding: 0px;

  .tab-main-div {
    // height: 100%;
    // width: 21%;
    // position: fixed;
    // margin-top: 0px;
    // padding-left: 0px;
    display: flex;
    width: 100%;
  }
  .right-accordian-view {
    height: calc(88vh - 88px);
    // border: solid 1px #f3eaea;
    width: 100%;
    // text-align: left;
    overflow: scroll;
  }

  .tablist {
    //  list-style-type: none;
    height: 40px;
    font-size: 14px;
    font-weight: 500;
    color: #666666;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 5.4%;
    border: 1px solid #ededed;
    border-left: none;
    background-color: #f8f8f8;
    cursor: pointer;
    width: 17%;
  }

  .tablist:last-child {
    border-bottom: 1px solid #e7e8f7;
  }

  .tab-panel {
    // margin-left: 27.5%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    //justify-content: center;
    //height: calc(88vh - 88px);
    // border: solid 1px #f3eaea;
    width: 100%;
    // text-align: left;
    overflow: scroll;
    color: #c3c3c3;
    border-bottom-right-radius: 10px;
    .no-signal {
      height: calc(88vh - 88px);
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &.react-tabs__tab-panel--selected {
      height: calc(88vh - 88px);
      > div {
        width: 100%;
      }
    }
  }
  .slider:hover {
    background-color: #ebedfd;
  }
  .react-tabs__tab--selected {
    background-color: #4a51c3;
    color: white;
    border-radius: 0px;
  }
  .tab-head-div {
    color: #666666;
    padding-left: 10%;
    padding-bottom: 6%;
    padding-top: 1%;
  }
}
